<template>
	<div>
		<div class="page-title">
			<h3>Категории</h3>
		</div>
		<section>
			<div v-if="loading">
				<Loader ></Loader>	
				<h4 class="text-center">Нет категорий</h4>
				
			</div>
			<div class="row" v-else>
				<CategoryCreate @addCategory="addCategoryToList"></CategoryCreate>
				<CategoryEdit :categories="categories"
					v-if="categories.length"
					:key="categories.length + lengthCount"
					@updateCategry="update"></CategoryEdit>

				
			</div>
		</section>
	</div>
</template>

<script>
	import CategoryCreate from '@/components/CategoryCreate'
	import CategoryEdit from '@/components/CategoryEdit'
	export default{
		data(){
			return{
				categories: [],
				loading: true,
				lengthCount: 0
			}
		},
		components:{
			CategoryCreate,
			CategoryEdit
		},
		methods: {
			addCategoryToList(cat){
				this.categories.push(cat)
				console.log(this.categories)
			},
			update(categoryData){
				console.log('update')
				this.categories.forEach((item) => {
					if(item.id === categoryData.id){
						item.title = categoryData.title
						item.limit = categoryData.limit
					}
				} )
				this.lengthCount++ //форсированная перерисовка компонента CategoryEdit путем добавления к нему :key="categories.length + lengthCount" 
			}
		},
	 	async mounted(){
			// const catsFromBase = await this.$store.dispatch('getCategories')
			// this.categories = Object.keys(catsFromBase)
			// for(let cat in catsFromBase){
			// 	this.categories.push({...catsFromBase[cat], id: cat})
			// }
			this.categories = await this.$store.dispatch('getCategories')
			// this.categories = Object.keys(catsFromBase).map((item) => ({...catsFromBase[item], id: item}))
			// console.log(catsFromBase)
			console.log(this.categories)
			this.loading = false
		}
	}
</script>

<style>
	.text-center{text-align: center;}
</style>