<template>
	<div class="col s12 m6">
					<div>
						<div class="page-subtitle">
							<h4>Создать</h4>
						</div>

						<form @submit.prevent="submitHandler">
							<div class="input-field">
								<input
								id="name"
								type="text"
								v-model="title"
								:class="{invalid: $v.title.$dirty && !$v.title.required}"
								>
								<label for="name">Название</label>
								<span class="helper-text invalid" 
									v-if="$v.title.$dirty && !$v.title.required">Введите название</span>
							</div>

							<div class="input-field">
								<input
								id="limit"
								type="number"
								v-model="limit"
								:class="{invalid: $v.limit.$dirty && !$v.limit.minValue}"
								>
								<label for="limit">Лимит</label>
								<span class="helper-text invalid"
									v-if="$v.limit.$dirty && !$v.limit.minValue">

									Минимальная величина</span>
							</div>

							<button class="btn waves-effect waves-light" type="submit">
								Создать
								<i class="material-icons right">send</i>
							</button>
						</form>
					</div>
				</div>
</template>

<script>
	import {required, minValue} from 'vuelidate/lib/validators'
	export default{
		data(){
			return{
				title: '',
				limit: 1
			}
		},
		mounted(){
			M.updateTextFields()//метод для очистки тексытовых полей из библиотеки Materialize
		},
		validations: {
			title: {required},
			limit: {minValue: minValue(1)}
		},
		methods: {
			async submitHandler(){
				if(this.$v.$invalid){
					this.$v.$touch()
					return
				}
				const newCategoryObj = {
					title: this.title,
					limit: this.limit
				}
				try{
					const readyCategory = await this.$store.dispatch('fetchCategory', newCategoryObj)
					this.title = ''
					this.limit = 1
					this.$v.$reset()//сброс флагов vuelidate
					this.$message(`Категория "${readyCategory.title}" была создана`)
					this.$emit('addCategory', readyCategory)
				}catch(e){

				}
			}
		}
	}
</script>