<template>
	<div class="col s12 m6">
					<div>
						<div class="page-subtitle">
							<h4>Редактировать</h4>
						</div>
						<!-- <ul>
							<li v-for="cat in categories">{{cat.title}}</li>
						</ul> -->

						<form @submit.prevent="submitHanler">
							<div class="input-field" >
								<select ref="select" v-model="current">
									<option v-for="cat in categories"
										 :key="cat.id"
										 :value="cat.id">{{cat.title}}</option>
								</select>
								<label>Выберите категорию</label>
							</div>

							<div class="input-field">
								<input
								id="name"
								type="text"
								v-model="title"
								:class="{invalid: $v.title.$dirty && !$v.title.required}"
								>
								<label for="name">Название</label>
								<span class="helper-text invalid" 
									v-if="$v.title.$dirty && !$v.title.required">Введите название</span>
							</div>

							<div class="input-field">
								<input
								id="limit"
								type="number"
								v-model="limit"
								:class="{invalid: $v.limit.$dirty && !$v.limit.minValue}"
								>
								<label for="limit">Лимит</label>
								<span class="helper-text invalid"
									v-if="$v.limit.$dirty && !$v.limit.minValue">

									Минимальная величина</span>
							</div>

							<button class="btn waves-effect waves-light" type="submit">
								Обновить
								<i class="material-icons right">send</i>
							</button>
						</form>
					</div>
				</div>
</template>

<script>
import {required, minValue} from 'vuelidate/lib/validators'

	export default{
		props: ['categories'],
		// props:{
		// 	categories: {
		// 		type: Array,
		// 		required: true
		// 	}
		// },
		data(){
			return{
				select: null,
				current: null,
				title: '',
				limit: 1
			}
		},
		watch: {
			current(currentID){
				console.log(this.categories.find((item) => item.id === currentID));
				const currentSelected = this.categories.find((item) => item.id === currentID)
				this.title =  currentSelected.title
				this.id =  currentSelected.id
				this.limit =  currentSelected.limit
			}
		},
		created(){
			console.log(Array.isArray(this.categories))
			console.log(this.categories[0].id)
			
			this.current = this.categories[0].id
			this.title = this.categories[0].title
			this.limit = this.categories[0].limit
			
			// const {id, title, limit} = this.categoryList[0]

		},
		mounted(){
			// this.categoryList.forEach((item, i, arr) => {
			// 	console.log(arr[i])
			// })
			M.updateTextFields()//метод для очистки тексытовых полей из библиотеки Materialize
			this.select = M.FormSelect.init(this.$refs.select);//игициализация кастомного селекта из библиотеки Materialize
		},
		destroyed(){
			if(this.select && this.select.destroy){
				this.select.destroy()
			}
		},
		methods: {
			async submitHanler(){
				if(this.$v.$invalid){
					this.$v.$touch()
					return
				}
				try{
					const categoryData = {
						id: this.current,
						title: this.title,
						limit: this.limit
					}
					await this.$store.dispatch('updateCategory', categoryData)

					this.$message('Категоря обновлена')
					this.$emit('updateCategry', categoryData)

				}catch(e){

				}
			}
		},
		validations: {
			title: {required},
			limit: {minValue: minValue(1)}
		}
	}
</script>